'use '
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation"; // Для маршрутизації
import { createClientComponentClient } from "@supabase/auth-helpers-nextjs"; // Імпорт Supabase
import Sidebar from "@/components/Sidebar";
// import type { Database } from "@/types/supabase"; // Ваші типи бази даних 

function LayoutWrapper({ children }: { children: React.ReactNode }) {
  const [isLoading, setIsLoading] = useState(true); // Стан завантаження
  const router = useRouter(); // Для маршрутизації
  const supabase = createClientComponentClient(); // Ініціалізація Supabase

  useEffect(() => {
    const checkAuth = async () => {
      const {
        data: { session },
      } = await supabase.auth.getSession(); // Отримання сесії користувача

      if (!session) {
        router.replace("/register"); // Перенаправлення на сторінку реєстрації
      } else {
        setIsLoading(false); // Завершення завантаження, якщо користувач автентифікований
      }
    };

    checkAuth();
  }, [router, supabase]);

  // Якщо завантаження триває, показати екран завантаження
  if (isLoading) {
    return <div>Loading...</div>;
  }

  // Якщо автентифікація успішна, відобразити контент
  return (
    <div className="layout-wrapper">
      <Sidebar />
      <main>{children}</main>
    </div>
  );
}

export default LayoutWrapper;
